* {
  margin: 0;
  padding: 0;
  font-family: 'Exo', sans-serif;
}

body {
  background: linear-gradient(to right, #00d877, #e7e308);
    /* -webkit-background-clip: text; */
    /* -webkit-text-fill-color: transparent; */
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.app .mainHeading h1 {
  text-align: center;
  margin-top: 3rem;
}

.app .subHeading {
  text-align: center;
  margin-top: 1rem;
}


.app h1,
h2 {
  color: black;
}

/* .app .input {
  display: flex;
  justify-content: center;
  align-items: center;
} */

.app .input {
  width: 500px;
  margin: auto;
  margin-top: 0.8em;
  height: 2em;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 6px;
  padding: 5px;
}

.app .input input {
  height: 1.6em;
  border-color: transparent;
  outline-color: transparent;
  padding-left: 1rem;
}

.app i {
  cursor: pointer;
  font-size: larger;
  font-weight: 900;
  color: black;
  margin-right: 5px;
}
.app .todos {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.app .todos .todo {
  margin-top: 0.8em;
  width: 20em;
  height: 2em;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 6px;
  padding: 5px;
}
.app .todos .todo .left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.app .todos .todo .left p {
  margin-left: 3px;
  font-size: medium;
  font-weight: 700;
  font-style: normal;
  color: grey;
}

.tick-text {
  text-align: center;
}

@media (max-width: 768px) {
  h2 {
    font-size: 18px;
  }
}

@media (max-width: 600px) {
  .app .input {
    /* margin: 2px; */
    margin-top: 0.8rem;
    width: 90%;
  }
}

@media (max-width: 500px) {
  h2 {
    font-size: 14px;
    text-align: center;
  }
  .app .mainHeading h1 {
    font-size: 28px;
  }
  .todos {
    margin:  0 12px;
  }
  .app .todos .todo {
    width: 100%;
  }
  .app .todos .todo .left {
    width: 13rem;
  }
}